
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {setTitle} from '@/common/js/common';

export default{
    data(){
        return{
          title:"",
          name:'',
          num:'',
          noticeid:'',
          maxnum:[],
          fenid:[],
          button_name:"",
          phone:'',
          adult:0,
          children:0,
          remark:'',
          confirm_show:false,
          dialog_title:"",
          confirm_content:"",
          type:0,
          isLoading:false,
          show_toast:false,
          toast_text:""
        }
    },
  components:{
    Group, XInput, XButton,XTextarea,Divider,XNumber,Alert,Loading,Toast
  },
  mounted(){

    document.getElementById("index_loading").style.display="none";
    this.noticeid=this.$route.query.noticeid;
    this.type=parseInt(this.$route.query.type);
    if(this.type===1){
        this.button_name="";
      setTitle("");
        if(service_url){
          this.$http.get(service_url+"/o2o-notice-webAndroid/wx/entryDetail?nid="+this.noticeid).then((data)=>{
            this.adult=data.body.data.adultcount;
            this.children=data.body.data.childcount;
            this.name=data.body.data.nname;
            this.phone=data.body.data.nphone;
            this.remark=data.body.data.remark;
          })
        }else{
          this.$http.get("/api/enroll").then((data)=>{
            this.adult=data.body.data.adultcount;
            this.children=data.body.data.childcount;
            this.name=data.body.data.nname;
            this.phone=data.body.data.nphone;
            this.remark=data.body.data.remark;
          })
        }
    }else{
      this.button_name="";
      setTitle("");
    }
  },
  methods:{
    do_submit(){
      let self =this;
      let myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
      if(!this.name){
        this.confirm_content="";
        this.confirm_show=true;
        return;
      }
      if(!this.phone||this.phone.length!==11||!myreg.test(this.phone)){
        this.confirm_content="";
        this.confirm_show=true;
        return;
      }
      if(!this.adult&&!this.children){
        this.confirm_content="";
        this.confirm_show=true;
        return;
      }
      this.isLoading=true;
      const senddata={
        "data":{
          "fenid":this.noticeid, //id   
          "name":this.name,//
          "phone":this.phone,//
          "adult":this.adult,//
          "children":this.children,//
          "remark":this.remark,//
        }
      };
      let url = service_url+"/o2o/notice/wx/action/entry?vid="+userProfile.vid+"&id="+this.noticeid;
      if(service_url){
        this.$http.post(url,senddata).then((data) => {
          this.isLoading=false;
          if(data.body.statusCode === "ok"){
            self.show_toast=true;
            setTimeout(this.go_back,1000);
          }else if(data.body.statusCode === "error"){
            this.confirm_content=data.body.message;
            this.confirm_show=true;
          }
        });
      }else{
        this.isLoading=false;
        self.show_toast=true;
        setTimeout(this.go_back,1000);
      }

    },
    go_back(){
      this.$router.go(-1);
    }
  }
}



